'use client'; // ユーザー入力(useState)を使うので必須

import { useState } from 'react';
import Link from 'next/link';

export default function KnotPage() {
  const [coeffs, setCoeffs] = useState('1,-3,1');
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleCalculate = async () => {
    setLoading(true);
    setResult(null);
    setError('');

    try {
      const res = await fetch(`/api?coeffs=${coeffs}`);
      if (!res.ok) throw new Error(`Server Error: ${res.status}`);
      const data = await res.json();
      setResult(data);
    } catch (err) {
      setError('計算に失敗しました。Vercel環境で実行してください。');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h2>Alexander多項式の根計算</h2>
      <p>Python (NumPy) を使用して、多項式の根を計算します。</p>

      <div style={{ padding: '20px', border: '1px solid #ccc', margin: '20px 0' }}>
        <label style={{ display: 'block', fontWeight: 'bold' }}>
          係数入力 (例: 1,-3,1)
        </label>
        <div style={{ display: 'flex', gap: '10px', marginTop: '10px' }}>
          <input 
            type="text" 
            value={coeffs}
            onChange={(e) => setCoeffs(e.target.value)}
            className="border p-2 flex-1"
          />
          <button 
            onClick={handleCalculate} 
            disabled={loading}
            className="bg-green-600 text-white px-4 py-2 rounded"
          >
            {loading ? '計算中...' : '計算実行'}
          </button>
        </div>

        {error && <p style={{ color: 'red', marginTop: '10px' }}>{error}</p>}

        {result && (
          <div style={{ marginTop: '20px', background: '#f0f9ff', padding: '15px' }}>
            <h4>結果:</h4>
            <ul>
              {result.roots?.map((root: any, i: number) => (
                <li key={i}>x = {root.string}</li>
              ))}
            </ul>
          </div>
        )}
      </div>

      <Link href="/" className="text-gray-500 hover:underline">
        &larr; ホームに戻る
      </Link>
    </div>
  );
}